import React, { Component } from 'react'
import { Button, Typography, withStyles } from '@material-ui/core';
import { withRouter } from 'react-router'
import { Link } from 'react-router-dom'
import { Map, EventSeat } from '@material-ui/icons';

const styles = theme => {
    return {
        container: {
            margin: `${theme.spacing.unit * 2}px`
        },
        instructions: {
            marginTop: `${theme.spacing.unit * 3}px`,
            textAlign: 'left'
        },
        iconText: {
            marginLeft: `${theme.spacing.unit}px`
        },
        inlineFlex: {
            display: 'inline-flex'
        }
    }
}

class Intro extends Component {
    constructor(props) {
        super(props);
        localStorage.clear();
    }

    render() {
        return (
            <div className={this.props.classes.container}>
                <Typography variant='h5' paragraph>
                    ¡Bienvenida a la búsqueda del tesoro!
                </Typography>
                <div className={this.props.classes.instructions}>
                    <Typography variant='body1' paragraph>
                        Antes que nada, ¡feliz primer año, amor! Fue hermoso y me hacés la persona más feliz del mundo.
                    </Typography>
                    <Typography variant='body1' paragraph>
                        <div>En la búsqueda vas a tener pistas:</div>
                        <div>
                            <div style={{ marginTop: '5px' }} className={this.props.classes.inlineFlex}>
                                <EventSeat color='primary' /><span className={this.props.classes.iconText}>en las que tenés que pensar</span>
                            </div>
                        </div>
                        <div>
                            <div className={this.props.classes.inlineFlex}>
                                <Map color='primary' /><span className={this.props.classes.iconText}>en las que tenés que ir a un lugar para encontrar la respuesta</span>
                            </div>
                        </div>
                        <div>
                            Cada pista te va a dar una de las llaves que sirven para abrir el cofre del tesoro. Espero que te traigan lindos recuerdos <span role='img' aria-label='smile'>😊</span>
                        </div>
                    </Typography>
                    <Typography variant='h5' paragraph align='center'>
                        <span role='img' aria-label='robotito'>🤖</span><span role='img' aria-label='corazón'>❤</span><span role='img' aria-label='maru'>🙃</span>
                    </Typography>
                    <Typography variant='body2' paragraph>
                        PD: Podés pedirme pistas por WhatsApp <span role='img' aria-label='smile'>😊</span>
                    </Typography>
                </div>
                <Link to='/questions'>
                    <Button color='primary' variant='contained' style={{ marginTop: '16px' }}>
                        Comenzar la búsqueda
                    </Button>
                </Link>
            </div>
        );
    }
}

export default withStyles(styles)(withRouter(Intro));
